        if (spcjd === false) {
            return false;
        }

        var rt = []; // return

        if (J !== undefined) {
            //,
            rt["pty"] = spcjd - ((this.J - this.floatval(J)) * 4) / 60 / 24; //,4
            rt["pty"] = this.Jtime(rt["pty"]); //

            spcjd = this.zty(spcjd, J, W); //,: ?
            rt["zty"] = this.Jtime(spcjd); //
        }

